@import url('https://fonts.cdnfonts.com/css/luna-2');

*{
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
  font-family: 'luna-2';
  font-style: normal;
  color: #FFFFFF;
  text-align: center;
}
body{
  width: 100vw;
  padding: 0 50px;
  background-image: url(./components/img/bgc.png);
}
h2{
  font-weight: 700;
  font-size: 64px;
  line-height: 140%;
}
h3{
  font-weight: 700;
  font-size: 48px;
  line-height: 140%;
}
p{
  font-weight: 400;
  font-size: 22px;
  line-height: 160%;
}
@media (max-width: 580px) {
  h2{font-size: 48px;}
  h3{font-size: 36px;}
}
@media (max-width: 480px) {
  h2{font-size: 36px;}
  h3{font-size: 24px;}
}



.navbar{
  background-color: black;
  width: 100%;
  padding: 20px 50px;
  position: fixed;
  right: 0;left: 0;top: 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
  z-index: 100;
}
nav{
  display: flex;
  align-items: center;
  justify-content: center;
}
ul li{
  margin: 0 8px;
  justify-content: space-between;
  display: inline;
  list-style-type: none;
}
ul li a{
  text-decoration: none;
  font-weight: 400;
  font-size: 22px;
  line-height: 150%;
}
@media (max-width: 800px) {
  ul li{display: block;}
  ul li a{font-weight: 700;font-size: 36px;}
  nav{
    position: fixed;
    padding: 25px;
    align-items: center;
    width: 100vw;height: auto;gap: 25px;
    top: 75px;z-index: 90;
    background-color: black;
    flex-direction: column;
    transform: translateX(100%);
    transition: transform 500ms;
    opacity: 0.8;
  }
}
nav.active{
  transform: translateX(-50px);
}

.timeline1, .timeline2, .timeline3{
  width: 100vw;
  height: 7.5px;
  left: 0;right: 0;
}
.timeline1{position: fixed;top: 80px;z-index: 100;}
.timeline2{position: absolute; top: calc(100vh - 7.5px);}
.timeline3{position: absolute;}
.Menu__icon{
  display: none;
  width: 40px;
  height: 30px;
  position: fixed;
  z-index: 101;
  top: 25px; right: 50px;
  cursor: pointer;
}
span{
  position: absolute;
  top: calc(50% - 1px);
  left: 0;
  width: 100%;
  height: 3px;
  background-image: url(./components/img/timeline.png);
}
span:first-child{top: 0;}
span:last-child{bottom: 0;top: auto;}

@media (max-width: 800px) {
  .Menu__icon{
    display: block;
  }
}
@media (max-width: 800px) {
  ul li a{font-size: 18;}
}
.cont__first{
  width: 100%;
  height: 100vh;
  padding: 10vh 0;
  gap: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.BackgraundImg{
  z-index: -1;
  height: 100vh;
  background: 70% 30% /cover;
  background-image: url(./components/img/main-screen__bgc.png);
  position: absolute;
  top: 0;right: 0;left: 0;
}
.about2{
  width: 100%;
  height: 100vh;
  padding: 10vh 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 50px;
}
.about2 p{
  width: 50%;height: auto;
}
@media (max-width: 1200px) {
  .about2 p{width: 100%;}
}
@media (max-width: 800px) {
  .about_img{width: 90%;height: auto;}
}
.takeprof{
  width: 100%;
  height: 100vh;
  padding: 10vh 0;
  gap: 75px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.profy{
  display: flex;
  flex-direction: row;
}
.practica{
  padding: 0 20px;
  gap: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  text-align: center;
}
@media (max-width: 1200px) {
  .profy{
    flex-direction: column;
    gap: 50px;
  }
  .practica p{width: 60%;}
}





.partners__logos{
  width: 100%;
  height: 100vh;
  padding: 10vh 0;
  gap: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.partnersimg{width: 100%;height: auto;}

.professors{
  width: 100%;
  height: 100vh;
  padding: 10vh 0;
  gap: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.cont_bio{
  font-size: 36px;
  width: 100%;
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-direction: row;
}
.biografia{
  margin: 0 15px;
  gap: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  text-align: center;
}
.biografia button{
  box-sizing: border-box;
  padding: 10px 30px;
  width: 143px;
  height: 44px;
  border: 1px solid #FFFFFF;
  border-radius: 50px;
  background-image: none; 
  background-color: transparent;
  font-weight: 400;
  font-size: 16px;
  line-height: 150%;
  flex: none;
  order: 0;
  flex-grow: 0;
}


@media (min-width: 1200px) {
  .cont_bio{font-size: 48px;}
  .cont_bio p{font-size: 36px;}
}
@media (max-width: 800px) {
  .cont_bio{flex-direction: column;gap: 100px;}
}


footer{
  width: 100%;
  height: 100%;
  padding: 10% 0;
  margin-top: 200px;
  gap: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.follow input{
  text-align: left;
  padding: 11px 30px;
  height: 44px;
  background: #000000;
  color: rgb(255, 255, 255);
  opacity: 0.5;
  font-size: 16px;
  border-radius: 50px 0px 0px 50px;
  z-index: 10;
}
.follow button{
  margin-left: -25px;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 10px 30px;
  height: 44px;
  background: linear-gradient(94.78deg, #DF5950 11.19%, #451046 93.72%);
  border-radius: 50px;
  font-weight: 700;
  font-size: 16px;
  line-height: 150%;
  flex: none;
  order: 0;
  flex-grow: 0;
  z-index: 11;
  cursor: pointer;
}
.follow{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
}
.icons{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
}
@media (max-width: 480px) {
  .icons{flex-direction: column;}
  .follow input{padding: 11px 0 11px 10px;}
  .follow button{padding: 10px 5px;}
}
.icons1, .icons2{display: flex;flex-direction: row;}
.icon{
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 15px;
  width: 50px;
  height: 50px;
  border-radius: 90%;
  background: linear-gradient(94.26deg, #C89AFC 9.51%, #7C6AFA 90.23%);
}








@media (max-height: 1600px) {
  .takeprof, .about2, .partners__logos, .professors{height: 100%; margin: 200px 0;}
}